<template>
  <div class="form__counter" :class="{ product__counter: styleBorder }">
    <button type="button" aria-label="Убрать один товар" @click="decrementAmount()" :disabled="amount === 1">
      <svg :width="iconSize" :height="iconSize" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" :value="amount" @input="inputAmount" :key="amount">

    <button type="button" aria-label="Добавить один товар" @click="incrementAmount()">
      <svg :width="iconSize" :height="iconSize" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: 'FormCounter',
  props: {
    amount: {
      type: Number,
      required: true
    },
    iconSize: {
      type: Number,
      default: 16
    },
    styleBorder: Boolean
  },
  methods: {
    inputAmount() {
      const parsedAmount = Number(event.target.value);
      if (!isNaN(parsedAmount)) {
        this.$emit('update:amount', parsedAmount);
      }
    },
    decrementAmount() {
      if (this.amount > 1) {
        this.$emit('update:amount', this.amount - 1);
      }
    },
    incrementAmount() {
      this.$emit('update:amount', this.amount + 1);
    }
  }
}
</script>
