<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ products.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <div v-if="isCartLoading">Загрузка товаров в корзине...</div>
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы посчитаем стоимость доставки на следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice |numberFormat }} ₽</span>
          </p>

          <router-link v-if="availabilityCartProducts" tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters, mapState } from "vuex";
import CartItem from "@/components/CartItem.vue";

export default {
  name: 'CartPage',
  components: {
    CartItem: CartItem,
  },
  filters: {
    numberFormat
  },
  computed: {
    availabilityCartProducts() {
      return this.$store.state.cartProducts.length > 0;
    },
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
    ...mapState(['isCartLoading'])
  }
}
</script>