<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
        {{ product.price | numberFormat }} ₽
      </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="productColor in product.colors" :key="productColor">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="colorsResult(productColor)" v-model="currentProductColor">
          <span class="colors__value" :style="{ 'background-color': colorsResult(productColor) }"></span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import colors from "@/data/colors";
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";

export default {
  name: 'ProductItem',
  props: ['product'],
  filters: {
    numberFormat
  },
  data() {
    return {
      currentProductColor: this.colorsResult(this.product.colors[0])
    }
  },
  watch: {
    productColor(value) {
      this.currentProductColor = value;
    }
  },
  methods: {
    gotoPage,
    colorsResult(productColors) {
      const color = colors.find(item => item.id === productColors);
      return color.value;
    }
  }
}
</script>
